import axios from 'axios'
import React, { useState, useEffect } from 'react'
import { useNavigate, Link } from 'react-router-dom';

function Read() {

    const [apiData, setApiData] = useState([])
    const [inputText, setInputText] = useState("");

    const navigate = useNavigate();

    


    function getData() {
        axios.get('https://64626dbb7a9eead6facf11a0.mockapi.io/crud')
            .then((response) => {
                setApiData(response.data);
            }).catch((err) => {
                console.log(err)
            });
    }

    function handleDelete(id) {
        axios.delete(`https://64626dbb7a9eead6facf11a0.mockapi.io/crud/${id}`)
            .then(() => {
                getData();
            }).catch((err) => {
                console.log(err)
            });
    }

    function setDataToStorage(id, name, age, email){
        localStorage.setItem('id',id);
        localStorage.setItem('name',name);
        localStorage.setItem('age',age);
        localStorage.setItem('email',email);
    }

    useEffect(() => {
        const sessionData = localStorage.getItem('userSession');

        if (sessionData) {
            const {id, username, loggedIn, expirationTime } = JSON.parse(sessionData);
            console.log(loggedIn,expirationTime, new Date().getTime() < expirationTime);
            if (loggedIn && new Date().getTime() < expirationTime) {
                getData();
                //navigate('/');
            }else{
                navigate('/login');
            }
        }else{
            navigate('/login');
        }
        //getData();
    }, [])

    const inputHandler = (e) => {
        setInputText(e.target.value.toLowerCase());
    };
    const handleLogout = (e) => {
        
        let text = "Are you sure to Logout ?";
        if (window.confirm(text) == true) {
            localStorage.removeItem('userSession');
            //navigate('/login');
            navigate('/logoutWindow');
        } else {
            
        }
        
    };
    const handleHome = (e) => {
        navigate('/home');
    };
    

    return (
        <>
            <div className="">
                <br className=""/>
                <br className=""/>

                <br className=""/>
                <div className="row align-items-center justify-content-center">
                    <div className="col-md-8">
                        <nav className="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow border border-1"> 
                            <a href="/" className="btn btn-floating btn-lg" style={{color: 'black', textDecoration: 'none', fontFamily: 'Itim, cursive', fontWeight: 'bold'}}>
                                <i className="fas fa-laugh-wink rotate-n-15" style={{color: 'black', fontSize: '25px'}} aria-hidden="true"/>
                                ReactCrud
                            </a>
                        
                            {/* Topbar Search */}
                            <form action="" className="d-none d-sm-inline-block form-inline mr-auto ml-md-3 my-2 my-md-0 mw-100 navbar-search">
                                <div className="input-group">
                                <input type="text" name="q" onChange={inputHandler} className="form-control bg-light border-0 small" placeholder="Search for..." aria-label="Search" aria-describedby="basic-addon2" required/>
                                <div className="input-group-append">
                                    <button className="btn btn-secondary" type="submit">
                                    <i className="fas fa-search fa-sm"></i>
                                    </button>
                                </div>
                                </div>
                            </form>

                            <Link to="/create">
                                <a href="" className="btn btn-floating btn-lg" style={{color: 'black', textDecoration: 'none', fontWeight: 'bold'}}>
                                    <i className="fas fa-plus" style={{color: 'black', fontSize: '25px'}}  aria-hidden="true"></i>
                                </a>
                            </Link>
                            
                            <ul className="navbar-nav ml-auto">
                               
                                <div className="topbar-divider d-none d-sm-block"></div>
                                
                                {/* Nav Item - User Information */}
                                <li className="nav-item dropdown no-arrow">
                                    <a className="nav-link" href="#" id="userDropdown" role="button" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        <span className="mr-2 d-none d-lg-inline text-gray-600 small font-weight-bolder">ajul</span>
                                        <img className="img-profile rounded-circle" width="40" src="data:image/png;base64,"/>
                                        <img className="img-profile rounded-circle" width="40"/>
                                    </a>
                                    {/* Dropdown - User Information */}
                                    <div className="dropdown-menu dropdown-menu-right shadow animated--grow-in" aria-labelledby="userDropdown">
                                        <a onClick={handleHome} className="dropdown-item">
                                        <i className="fas fa-user fa-sm fa-fw mr-2 text-gray-400"></i>
                                        Profile
                                        </a>
                                        <a className="dropdown-item">
                                        <i className="fas fa-cogs fa-sm fa-fw mr-2 text-gray-400"></i>
                                        Settings
                                        </a>
                                        <a className="dropdown-item">
                                        <i className="fas fa-list fa-sm fa-fw mr-2 text-gray-400"></i>
                                        Activity Log
                                        </a>
                                        <div className="dropdown-divider"></div>
                                        
                                        <a onClick={handleLogout} className="dropdown-item">
                                        <i className="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400"></i>
                                        Logout
                                        </a>
                                    </div>
                                </li>
                            </ul>
                        </nav>
                        {/* End of Topbar */}
                    </div>
                </div>

                <div class="row align-items-center justify-content-center mb-2 d-sm-none">
                    <div class="col-md-8">
                        <div className="input-group">
                            <input type="text" name="query" onChange={inputHandler} className="form-control bg-light border-0 small" placeholder="Search for..." aria-label="Search" aria-describedby="basic-addon2" required/>
                            <div className="input-group-append">
                                <button className="btn btn-secondary" type="submit">
                                    <i className="fas fa-search fa-sm"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>

                {/* users */}
                <div className="row align-items-center justify-content-center">
                    <div className="col-md-8 overflow-auto" style={{maxHeight: '410px'}}>
                        {/* card */}
                        {
                            apiData.filter((el) => {
                                if (el === "") {
                                  return el;
                                } else {
                                  return (
                                    el.e_name.toLowerCase().includes(inputText) ||
                                    el.e_email.toLowerCase().includes(inputText)
                                  );
                                }
                              }).slice(0).reverse().map((item) => {
                                return (
                                    <>
                                        <div className="card gedf-cardd mb-2">
                                            <div className="card-header">
                                                <div className="d-flex justify-content-between align-items-center">
                                                    <div className="d-flex justify-content-between align-items-center">
                                                        <div className="mr-2">
                                                            <img className="rounded-circle" width="45" src="https://picsum.photos/50/50" alt=""/>
                                                        </div>
                                                        <div className="ml-2">
                                                            <div className="h5 m-0">
                                                                <a data-bs-toggle="collapse" href={'#'+item.id} role="button" aria-expanded="false" aria-controls="collapseExample" style={{color: 'black'}}>{item.e_name}</a>
                                                            </div>
                                                            {/* <div className="h7 text-muted">ID : {item.id} | Age : {item.e_age} | {item.e_email}</div> */}
                                                            <small className="h7 text-muted"> {item.e_email}</small>

                                                        </div>
                                                        
                                                    </div>
                                                    {/*<div>
                                                        <Link to='/edit'>
                                                            <button className='btn btn-secondary' onClick={() => setDataToStorage(item.id, item.e_name, item.e_age, item.e_email)}>Edit</button>
                                                        </Link>
                                                        <button className='btn btn-outline-danger ml-2' onClick={() => { if (window.confirm('Are You Sure To Delete Data ??')) { handleDelete(item.id) } }}>Delete</button>
                                                    </div>
                                                    */}
                                                    
                                                    <div className="btn-group" role="group" aria-label="First group">
                                                        <Link to='/edit'>
                                                            <button type="button" 
                                                            className="btn btn-secondary bg-secondary bg-gradient"
                                                            onClick={() => setDataToStorage(item.id, item.e_name, item.e_age, item.e_email)}>
                                                                <i className="fas fa-edit fa-sm"></i>
                                                            </button>
                                                        </Link>    
                                                        <button type="button" 
                                                        className="btn btn-secondary bg-secondary bg-gradient"
                                                        onClick={() => { if (window.confirm('Are You Sure To Delete Data ??')) { handleDelete(item.id) } }}>
                                                            <i className="fas fa-trash fa-sm"></i>
                                                        </button>
                                                      </div>
                                                </div>
                                            </div>

                                            {/* collapse - collapseExample*/}
                                            <div class="collapse" id={item.id} data-bs-parent="#accordion">
                                                <div class="card card-body text-muted">
                                                ID : {item.id} | Age : {item.e_age} | {item.e_email}
                                                </div>
                                            </div>
                                            {/* end collapse */}
                                        </div>
                                    </>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        </>
    )
}

export default Read